import * as chroma from 'chroma-js'

export let red = chroma('red').luminance(0.4)
export let blue = chroma('blue').luminance(0.4)
export let scaleRed = chroma.scale(['white', red]).mode('lch')
export let scaleBlue = chroma.scale(['white', blue]).mode('lch')
export let textred = chroma('red').luminance(0.5)
export let textblue = chroma('blue').luminance(0.5)
export let scaleTextRed = chroma.scale(['white', textred]).mode('lch')
export let scaleTextBlue = chroma.scale(['white', textblue]).mode('lch')

export function colorExtend(color, x0, x1, y0, y1) {
  return `-${x0}px -${y0}px 0 ${color}, ${x1}px -${y0}px 0 ${color}, ${x1}px ${y1}px 0 ${color}, -${x0}px ${y1}px 0 ${color}`
}
